import styled from 'styled-components';
import { Box, Button, Text } from '@pancakeswap/uikit';

const Container = styled(Box)`
  max-width: 350px;
    align-items: right;
  margin: 10px auto;
  padding: 10px;
  border: 2px solid #5a9f98;
  justify-content: right;
  border-radius: 12px;
  background-color: #000;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  color: #fff;
`;
const DepositList = styled.ul`
  list-style: none;
  padding: 0;
  margin-top: 20px;
  background-color: #1e1e1e;
  border-radius: 8px;
    font-size: 0.9rem;

  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  color: #e5e5e5;

  li {
    padding: 15px;
    border-bottom: 1px solid #333;
    
    &:last-child {
      border-bottom: none;
    }

    strong {
      color: #62d5a3;
    }
  }
`;
const ArrowButton = styled(Button)`
  background-color: #62d5a3;
  color: #1e1e1e;
  font-weight: bold;
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;

  &:hover {
    background-color: #4abf88;
    transform: translateY(-2px);
  }

  &:active {
    background-color: #3aa47b;
    transform: translateY(0);
  }

  &:disabled {
    background-color: #3aa47b;
    cursor: not-allowed;
  }
`;

const HeaderContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
`;

const Title = styled.h2`
  margin-bottom: 0;
  font-size: 1.2rem;
  color: #62d5a3;
`;

const ArrowIcon = styled.span`
  margin-left: 10px;
  font-size: 1.5rem;
  color: #62d5a3;
  display: flex;
  align-items: center;
`;

const Content = styled.div`
  margin-top: 20px;
`;

const ImageGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
  gap: 10px;
  margin-top: 20px;
`;

const ImageItem = styled.div`
  text-align: center;

  img {
    width: 80px;
    height: 80px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }

  p {
    color: #e5e5e5;
    font-size: 0.9rem;
    margin-top: 10px;
  }
`;

const StyledButton = styled(Button)`
  width: 100%;
  padding: 12px;
  font-size: 1rem;
  font-weight: bold;
  background-color: #62d5a3;
  color: #1e1e1e;
  border-radius: 8px;
  margin-top: 10px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;

  &:hover {
    background-color: #4abf88;
    transform: translateY(-2px);
  }

  &:active {
    background-color: #3aa47b;
    transform: translateY(0);
  }
`;

const InfoText = styled(Text)`
  font-size: 1.2rem;
  color: #e5e5e5;
`;

const InfoText2 = styled(Text)`
  font-size: 1.2rem;
  color: #e5e5e5;
`;
export { Container, HeaderContainer, Title, ArrowIcon, Content, ImageGrid, ImageItem, StyledButton, InfoText,InfoText2, DepositList, ArrowButton };
